var loginRes, _self;

export default {
		components: {
			uniSwipeAction,
		},
	data() {
		return {
			storageData: {},
			avatar: '',
			nickname: '',
			//分类信息
			categories: [{ cateid: 0, name: '文章' }, { cateid: 1, name: '关注' }, { cateid: 2, name: '收藏' }, { cateid: 3, name: '积分' }],
			// 当前选择的分类
			cateCurrentIndex: 0,
			articles: [],
			follows: [],
			option: [{
					text: '删除',
					style: {
						backgroundColor: '#dd524d'
					}
				}]
		};
	},
	onLoad: function() {},
	onShow: function() {
		var _this = this;
		const loginKey = uni.getStorageSync('login_key');
		if (loginKey) {
			this.storageData = {
				login: loginKey.login,
				nickname: loginKey.nickname,
				avatar: loginKey.avatar,
				userId: loginKey.userId
			};
			uni.request({
				url: this.apiServer + '/article/user_count',
				method: 'GET',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					userId: this.storageData.userId
				},
				success: res => {
					_this.articleCount = res.data.data;
				}
			});
			uni.request({
				url: this.apiServer + '/article/user',
				method: 'GET',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					userId: this.storageData.userId
				},
				success: res => {
					_this.articles = res.data.data;
				}
			});
			uni.request({
				url: this.apiServer + '/follow/list',
				method: 'GET',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					fromUId: this.storageData.userId
				},
				success: res => {
					_this.follows = res.data.data;
				}
			});
		} else {
			this.storageData = {
				login: false
			};
		}
		uni.request({
			url: 'http://127.0.0.1:8080/api/user/' + uni.getStorageSync('login_key').userId,
			method: 'GET',
			header: { 'content-type': 'application/json' },
			success: res => {
				if (res.data.code === 0) {
					console.log(res.data.data.avatar+'————————————');
					_this.avatar = res.data.data.avatar;
					_this.nickname = res.data.data.nickname;
				}
			}
		});
	},
	methods: {
		tabChange: function(e) {
			// 选中的索引
			var index = e.currentTarget.dataset.index;
			// 具体的分类id
			var cateid = e.currentTarget.dataset.cateid;
			this.cateCurrentIndex = index;
			// 动态替换内容
			this.content = this.categories[index].name;
		},
		gotoDetail: function(aId) {
			uni.navigateTo({
				url: '../detail/article_detail?aId=' + aId + '&userId=' + this.storageData.userId
			});
		},
		deleteArticle:function(id){
				uni.request({
				url: this.apiServer + '/article/delete',
				method: 'DELETE',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					id:id
				},
				success: res => {
					if (res.data.code === 0) {
						uni.showToast({
							title: '文章删除成功'
						});
					}
					this.articles.splice(1,1);
				}
			});
		}
	}
};
